.Login {
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 5rem;
    .LogginButton{
        cursor: pointer;
        padding: 3px;
        max-height: 5vh;
        width: max-content;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            max-height: 100%;
            max-width: auto;
        }
        background-color: white;
        border-radius: 5px;
        box-shadow: 0px 0px 5px black;
    }

    @keyframes spinner {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }
      .loading_spinner {
        width: 50px;
        height: 50px;
        border: 10px solid #f3f3f3; /* Light grey */
        border-top: 10px solid #383636; /* Black */
        border-radius: 50%;
        animation: spinner 1.5s linear infinite;
      }
}